.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 1); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; 
}

.modal-content {
  background: white;
  padding: 40px; /* Увеличиваем внутренние отступы */
  border-radius: 8px;
  width: 600px; /* Увеличиваем ширину модального окна */
  max-width: 90%; /* Максимальная ширина для мобильных устройств */
  position: relative;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: transparent;
  font-size: 24px;
  cursor: pointer;
}

button {
  padding: 10px 15px; /* Увеличиваем внутренние отступы кнопок */
  margin-top: 10px; /* Отступ сверху для кнопок */
  margin-right: 5px; /* Отступ справа для кнопок */
  border: none;
  border-radius: 5px; /* Закругленные углы */
  background-color: #007bff; /* Цвет кнопки */
  color: white; /* Цвет текста кнопки */
  cursor: pointer; /* Курсор при наведении */
  transition: background-color 0.3s; /* Плавный переход цвета */
}

button:hover {
  background-color: #0056b3; /* Цвет кнопки при наведении */
}

.modal-exit {
  padding: 10px 15px; /* Внутренние отступы для кнопки */
  margin-top: 20px; /* Отступ сверху */
  border: none;
  border-radius: 5px; /* Закругленные углы */
  background-color: #dc3545; /* Цвет кнопки (красный) */
  color: white; /* Цвет текста кнопки */
  cursor: pointer; /* Курсор при наведении */
  transition: background-color 0.3s; /* Плавный переход цвета */
}

.modal-exit:hover {
  background-color: #c82333; /* Цвет кнопки при наведении */
}